<template>
  <div class="xfgj" v-if="list.length">
    <div class="container">
      <ul class="list clearfloat">
        <li class="item-li" v-for="(item,index) of list" :key="index" :style="itemLeft(index)">
          <div class="inner" :class="popShowIndex===index?'active':''">
            <p class="time">重复次数：{{item[getKey('重复信访次数')]}}</p>
            <i class="icon"></i>
            <p class="date">{{item[getKey('信访日期')]}}</p>
            <i class="circle" @click="timeChange(index,item[getKey('信访事项id')])"></i>
          </div>
          <transition name="fade">
            <div>
              <div class="arrow" v-if="popShowIndex===index"></div>
              <ul
                class="pop clearfloat"
                v-if="popShowIndex===index&&detailData"
                :style="{left:popLeft(index)+'px'}"
              >
                <li class="pop-li" v-for="(it,i) of Object.keys(detailData.map)" :key="i">
                  <p class="name">{{detailData.map[it]}} : </p>
                  <p class="value" :title="it.value">{{detailData.data[0][it]}}</p>
                </li>
              </ul>
            </div>
          </transition>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";

  export default {
    name: "xfgj",
    props: {
      data: Object
    },
    data() {
      return {
        xfsxid: "",
        popShowIndex: 3,
        detailData: null
      }
    },
    mounted() {
      if (this.list.length) {
        this.popShowIndex = this.list.length - 1;
        this.xfsxid = this.list[this.popShowIndex][this.getKey('信访事项id')];
        console.log(this.xfsxid);
        this.getDetailAPI(this.xfsxid);
      }
    },
    methods: {
      getDetailAPI(id) {
        get_http("qjywjkXfrhxXfgjDetail", {xfsxid: id})
          .then(res => {
            this.detailData = res.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 时间切换
      timeChange(index, id) {
        this.xfsxid = id;
        this.popShowIndex = index;
        this.getDetailAPI(this.xfsxid);
      },
      // 时间轴
      getKey(name) {
        for (let i in this.data.map) {
          if (this.data.map[i] === name) {
            return i
          }
        }
      },
      popLeft(index) {
        if (index === this.list.length - 1) {
          return -400
        }
        if (this.list.length === 3) {
          if (index === 1) {
            return -200
          }
        }
        if (this.list.length === 4) {
          if (index === 1) {
            return -290
          } else if (index === 2) {
            return -360
          }
        }
      },
      itemLeft(index) {
        if (this.list.length === 3) {
          if (index === 1) {
            return {
              position: "absolute",
              left: "50%"
            }
          }
        }
        if (this.list.length === 4) {
          if (index === 1) {
            return {
              position: "absolute",
              left: "40%"
            }
          } else if (index === 2) {
            return {
              position: "absolute",
              left: "63%"
            }
          }
        }
      },
    },
    computed: {
      list() {
        return this.data.data
      },
      styleType() {
        return this.$store.state.styleType;
      }
    },
    watch: {
      list: {
        handler(newVal, oldVal) {
          if (newVal.length) {
            this.popShowIndex = newVal.length - 1;
            this.xfsxid = newVal[this.popShowIndex][this.getKey('信访事项id')];
            this.getDetailAPI(this.xfsxid);
          }
        },
        deep: true
      }
    }
  }
</script>

